import { createGlobalStyle, css } from "styled-components";

const devicesSizes = {
  small: 576,
  medium: 768,
  large: 992,
  xLarge: 1200,
};

const devices = Object.entries(devicesSizes).reduce(
  (acc, [deviceName, deviceSize]) => ({
    ...acc,
    [deviceName]: {
      size: deviceSize,
      query: `(min-width: ${deviceSize}px)`,
    },
  }),
  {}
);

export const ThemeGlobalStyle = createGlobalStyle`
  @import url('https://fonts.googleapis.com/css?family=Roboto');

  * {
    box-sizing: border-box;
  }

  html,
  body {
    margin: 0;
    padding: 0;
    height: 100%;

    font-family: 'Roboto', sans-serif;
    font-size: large;
    color: ${({ theme }) => theme.colors.body.text};
    background-color: ${({ theme }) => theme.colors.body.bg};
  }

  h1, h2, h3, h4 {
    color: ${({ theme }) => theme.colors.titles.text};
  }

  #root {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    --grid-gutter: 16px;
  }
`;

export function imageSizes(config, fallback) {
  return Object.entries(devices)
    .reduce((acc, [deviceName, { size, query }]) => {
      const fn = config[deviceName];
      if (!fn || typeof fn !== "function") return acc;

      return [...acc, `${query} ${fn(size)}`];
    }, [])
    .concat([fallback]);
}

const clearfix = css`
  &::before,
  &::after {
    content: "";
    clear: both;
    display: table;
  }
`;

export default {
  devices,
  clearfix,
  colors: {
    body: {
      bg: "#fff",
      text: "#353535",
    },
    titles: {
      text: "#FF7607",
    },
    appBanner: {
      bg: "#CEE0DC",
      text: "#353535",
      title: "#353535",
      subtitle: "#fff",
    },
    appMenu: {
      bg: "#A2C5AC",
      text: "#fff",
      highlight: "#F77518",
    },
    appFooter: {
      bg: "#A2C5AC",
      text: "#fff",
    },
  },
};
